<script>
import { mapState } from 'vuex';
import HtmlWithLinks from '@/components/HtmlWithLinks.vue';
import SideloadingOptions from '@/components/SideloadingOptions.vue';

export default {
  components: {
    HtmlWithLinks,
    SideloadingOptions,
  },
  data() {
    return {
      officialOptions: [],
      thirdPartyOptions: [],
      plusOptions: [],
      devCredentialsProviders: [],
    };
  },
  computed: {
    title() {
      return this.$t('HOW_TO_SIDELOAD_AND_USE_PLUS');
    },
    ...mapState(['theme', 'currentDevice', 'config']),
    installationMethod() {
      // Get installation method from either currentDevice or directly from config
      return (this.currentDevice && this.currentDevice.app_installation_method) ||
        (this.config && this.config.app_installation_method) || '';
    },
    isOfficialMethod() {
      const method = this.installationMethod;
      return ['dev_account'].includes(method);
    },
    isP2PMethod() {
      const method = this.installationMethod;
      return ['shared_dev_account'].includes(method);
    },
    isCertificateMethod() {
      const method = this.installationMethod;
      return method === 'certificate';
    },
    isEnterpriseMethod() {
      const method = this.installationMethod;
      return method === 'enterprise';
    }
  },
  methods: {
    purchase(e) {
      if (!e.target?.matches('a[href="#"]')) {
        return;
      }
      e.preventDefault();

      this.$alert(`<div class="g-text">${this.$t('PLUS_DOWNLOAD_APP', { href: 'https://apps.apple.com/us/app/apple-developer/id640199958' })}</div>`);
    },
    importDevCredetials(href) {
      this.$confirm(this.$t('IMPORT_DEV_CREDENTIALS_PROVIDER_CONFIRMATION'))
        .then(() => {
          window.location.href = href;
        });
    },
    scrollToHash() {
      if (window.location.hash) {
        const id = window.location.hash.substring(1);
        const element = document.getElementById(id);
        if (element) {
          setTimeout(() => {
            const yOffset = -20;
            const y = element.getBoundingClientRect().top + window.pageYOffset + yOffset;
            window.scrollTo({ top: y, behavior: 'smooth' });
          }, 300);
        }
      }
    }
  },
  created() {
    this.$api.getSideloadingOptions('official').then((response) => {
      this.officialOptions = response;
    });

    this.$api.getSideloadingOptions('3rdparty').then((response) => {
      this.thirdPartyOptions = response;
    });

    /* this.$api.getDevCredentialsProviders()
      .then((response) => {
        this.devCredentialsProviders = response;
      }); */

    this.$api.getSideloadingOptions('plus').then((response) => {
      this.plusOptions = response;
    });
  },
  mounted() {
    this.scrollToHash();
  },
  updated() {
    this.scrollToHash();
  }
};
</script>

